import  { Button } from './ButtonLike.style';
import Like from '../../static/images/home/like.svg';

export default class ButtonLike extends React.component {

  constructor(props, context) {
    super(props, context);
  }

  render () {
    const  { like, action } = this.props;

    return (
      <Button  color={like}>
         <Like className="like" />
      </Button>
    )
  }

}



